import {HttpClient} from '@angular/common/http'
import {Injectable} from '@angular/core'

/*
  Generated class for the RestProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class RestProvider {
    apiUrl = 'http://duckapi.thibaudcordina.fr'

    constructor(public http: HttpClient) {}

    getRestos() {
        return new Promise(resolve => {
            this.http
                .get(this.apiUrl + '/restaurants', {
                    params: {
                        token: 'test'
                    }
                })
                .subscribe(
                    data => {
                        resolve(data)
                        return data
                    },
                    err => {
                        console.log(err)
                    }
                )
        })
    }

    getResto(id) {
        return new Promise(resolve => {
            this.http
                .get(this.apiUrl + '/restaurants/' + id, {
                    params: {
                        token: 'test'
                    }
                })
                .subscribe(
                    data => {
                        resolve(data)
                        return data
                    },
                    err => {
                        console.log(err)
                    }
                )
        })
    }

    getCategories() {
        return new Promise(resolve => {
            this.http
                .get(this.apiUrl + '/categories', {
                    params: {
                        token: 'test'
                    }
                })
                .subscribe(
                    data => {
                        resolve(data)
                        return data
                    },
                    err => {
                        console.log(err)
                    }
                )
        })
    }

    postLogin(email, password) {
        console.log('infos', email, password)
        return new Promise(resolve => {
            this.http
                .post(this.apiUrl + '/login', {
                    params: {
                        _email: email,
                        _password: password,
                    }
                })
                .subscribe(
                    data => {
                        resolve(data)
                        console.log(data)
                        return data
                    },
                    err => {
                        console.log(err)
                    }
                )
        })
    }

    postRegister(formData) {
        return new Promise(resolve => {
            this.http
                .post(this.apiUrl + '/register', {
                    params: {
                        email: formData['email'],
                        password: formData['password'],
                        nom: formData['nom'],
                        prenom: formData['prenom'],
                    }
                })
                .subscribe(
                    data => {
                        resolve(data)
                        console.log(data)
                        return data
                    },
                    err => {
                        console.log(err)
                    }
                )
        })
    }

}



